.heading, .loading {
  text-align: center;
}
.animals {
  max-width: 240px;
}
.wrapper {
  max-width: 700px;
  margin:0 auto ;
}

img {
  width: 100%;

  padding-top: 10px;
  
}
a {
  text-decoration: none;
  color: black;
}
h2 {
  font-size: 18px;
}

/* Details Page */
.detail-items {
  text-align: center;
  margin: 0 auto;
  max-width: 800px;
}
/* contact */

form, fieldset{
  max-width: 600px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

span {
